"use client";

import React from "react";
import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  CartesianGrid,
} from "recharts";

export default function ProgressChart({
  data,
  height = 160,
}: {
  data: { label: string; value: number }[];
  height?: number;
}) {
  return (
    <div style={{ width: "100%", height }}>
      <ResponsiveContainer width="100%" height="100%">
        <LineChart data={data} margin={{ top: 6, right: 6, left: 6, bottom: 6 }}>
          <CartesianGrid strokeDasharray="3 3" stroke="#F1F1F1" />
          <XAxis dataKey="label" tick={{ fill: "#9A9A9A" }} />
          <YAxis tick={{ fill: "#9A9A9A" }} />
          <Tooltip />
          <Line type="monotone" dataKey="value" stroke="#00C974" strokeWidth={3} dot={{ r: 3 }} />
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
}
