"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import ResultUpsell from "@/components/motivation/ResultUpsell";

export default function QuizResultPage() {
  const router = useRouter();
  const [quizData, setQuizData] = useState<any>(null);

  useEffect(() => {
    const data = localStorage.getItem("quizData");
    if (data) {
      setQuizData(JSON.parse(data));
    }
  }, []);

  if (!quizData) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <p>Carregando...</p>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      <div className="max-w-2xl mx-auto px-6 py-12">
        <div className="text-center space-y-6">
          <div className="w-20 h-20 bg-[#00C974] rounded-full mx-auto flex items-center justify-center">
            <svg className="w-10 h-10 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
            </svg>
          </div>

          <h1 className="text-3xl font-bold text-gray-900">Seu plano inicial está pronto!</h1>

          <p className="text-gray-600">Montamos uma recomendação inicial com base nas suas respostas. Veja um exemplo de como podemos acelerar seus resultados:</p>
        </div>

        {/* Componente de upsell + gráfico */}
        <div className="mt-8">
          <ResultUpsell
            quizData={quizData}
            onStartTrial={() => {
              // exemplo: redirecionar para página de checkout/plano
              router.push("/pricing?utm_source=quiz&utm_campaign=start_trial");
            }}
            onSeePlans={() => router.push("/pricing?utm_source=quiz&utm_campaign=see_plans")}
          />
        </div>

        {/* Resumo breve */}
        <div className="bg-gray-50 rounded-2xl p-6 mt-8 text-left">
          <h3 className="text-lg font-semibold text-gray-900 mb-3">Resumo das suas respostas</h3>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <div className="text-sm text-gray-500">Objetivo</div>
              <div className="font-medium text-gray-900">{quizData.objetivo}</div>
            </div>
            <div>
              <div className="text-sm text-gray-500">Refeições recomendadas</div>
              <div className="font-medium text-gray-900">{quizData.refeicoes_dia} refeições</div>
            </div>

            <div>
              <div className="text-sm text-gray-500">Nível de treino</div>
              <div className="font-medium text-gray-900">{quizData.nivel_treino}</div>
            </div>

            <div>
              <div className="text-sm text-gray-500">Frequência</div>
              <div className="font-medium text-gray-900">{quizData.frequencia_treino}</div>
            </div>
          </div>
        </div>

        <div className="mt-6 flex gap-3">
          <Button className="w-full bg-[#00C974] rounded-full" onClick={() => router.push("/register")}>
            Criar minha conta para continuar
          </Button>
        </div>
      </div>
    </div>
  );
}
